@import '../../../styles/Main.scss';

.UserImage{
    width: 190px;
    height: 240px;
    margin: auto;
    overflow: hidden;
    border-radius: 20px;

    &__img{
        width: 190px;
        height: auto;
    }
}

@media (max-width: 1280px) {
    .UserImage{
        height: 200px;
    }
}