@import '../Main.scss';

.headerCoin{
    width: 96%;
    height: 130px;
    margin: auto;
    display: flex;
    padding: 20px 30px;
    margin-bottom: 30px;
    background-color: $background_color;

    &__image{
        width: 80px;
        height: auto;
    }

    &__image>img{
        width: 100%;
        height: auto;
    }

    &__name{
        width: 30%;
        display: flex;
        padding: 0 15px;
    }

    &__name>span{
        margin: auto 0;
       @include textInfo();
    }
}

@media (max-width: 768px) {
    .headerCoin{
        align-items: center;
        flex-direction: column;
        height: auto;

        &__name{
            width: auto;
            padding: 0;
        }
    }
}

@media (max-width: 440px) {
    .otherCoins{
        width: 100%;
    }
}